<template>
<div> 
  <v-client-table :columns="columns" :data="data" :options="options" :theme="theme" id="dataTable" class="curriculum_table">
    <div slot="headings" slot-scope="props">
        {{props.headings}}
    </div>

    <div slot="topic" slot-scope="props">
      <span v-for="(topic, index) in props.row.topics" :key="index">
        <div class="tag-group-name">{{topic}}</div>
      </span>  
    </div>
      
    <div slot="child_row" slot-scope="props">
      <div class="row-expanded-container">
        <div class="curric-list-column-1">
          <div class="curric-list-section">
            <div class="curric-list-section-title">Class Description</div>
            <!--<div class="curric-list-section-content">{{converHTML(props.row.description)}}</div>-->
            <div v-if="!readMoreActivated">{{converHTML(props.row.description).slice(0, 200)}}</div>
            <a class="" v-if="!readMoreActivated && props.row.description != ''" @click="activateReadMore" href="#">
              read more...
            </a>
            <div v-if="readMoreActivated" v-html="converHTML(props.row.description)"></div>
            <a class="" v-if="readMoreActivated" @click="activateReadMore" href="#">
              read less...
            </a>
          </div>
          <div class="curric-list-section">
            <div class="curric-list-sub-section-size-half">
              <div class="curric-list-section-title">Assigned date</div>
              <div class="curric-list-sectino-content">{{ props.row.assignDate }}</div>
            </div>
            <div class="curric-list-sub-section-size-half">
              <div class="curric-list-section-title">Assigned by</div>
              <div class="curric-list-sectino-content">{{ props.row.assignedBy }}</div>
            </div>
          </div>
        </div>
        <div class="curric-list-button-controls">
          <b-button size="sm" variant="danger" class="dash-btn" @click="showRemoveClassModal(props.row)">REMOVE CLASS</b-button>
        </div>
        <b-modal title="Are you sure?" class="modal-primary" v-model="removeClassModal" @ok="removeClassModal = false; removeClass()" ok-variant="danger">
          Confirm that you want to Remove Class {{selectedClassName}}
        </b-modal>
      </div>
    </div>
  </v-client-table>
   <div class="vld-parent">
        <loading :active.sync="isLoading" 
        :can-cancel="true" 
        :is-full-page="fullPage"
        color="#06b">
        </loading>
    </div>
</div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import {ClientTable, Event} from 'vue-tables-2'
import { AsideToggler,  Aside as AppAside, } from '@coreui/vue'
import {restapiPrefix,wprestapiPrefix} from './variables.js'
// import DefaultAside from './DefaultAside'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import miniToastr from 'mini-toastr';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}
Vue.use(ClientTable)

export default {
  name: 'CurriculumTable',
  props: ["columns", "data", "options","theme"],
  components: {
    ClientTable,
    Event,
    AsideToggler,
    AppAside,
    Loading
  },
  data () {
    return {
      // msg: 'Widgets'
      readMoreActivated: false,
      removeClassModal: false,
      selectedClassName: "",
      selectedClassId: "",
      is_initial: 1,
      userId: 1,
      classIdList : [],
      assignedBy: "Segun Ogungbemi",
      isLoading: false,
      fullPage: true,
    }
  },
  created(){
     this.userId = localStorage.user_id;
     this.assignedBy = localStorage.user_nm;
  },
  methods: {
    rotateIcon: function (event) {
      event.toggleClass("down");
    },
    activateReadMore(){
        this.readMoreActivated = !this.readMoreActivated ;
    },
    converHTML(str){
      const doc = new DOMParser().parseFromString(str, "text/html");
      const textContent = doc.documentElement.textContent;
      //console.log(textContent);
      return textContent;
    },
    showRemoveClassModal: function (course) {
      this.removeClassModal = true;
      this.selectedClassName = course.class_name;
      this.selectedClassId = course.id;
      // this.selectedUserId = caregiver.user_id;
    },
    removeClass()
    {
      this.isLoading = true;
      let asksUri = restapiPrefix+'/caregiverCurriculum/getAllCurriculumsByUserIdAndInitial/'+this.userId+'/'+this.is_initial;
      this.axios.get(asksUri).then(response => {
        this.caregiverCurriculums = response.data;
        console.log(this.caregiverCurriculums);
        let class_list = [];
        for(let i = 0; i < this.caregiverCurriculums.length; i++) {
          if(this.caregiverCurriculums[i].class_id != this.selectedClassId)
          {
            class_list.push(this.caregiverCurriculums[i].class_id);
          }
        }
        console.log(class_list);

        var date = new Date().toJSON().slice(0,10).replace(/-/g,'/')
        let asksUri = restapiPrefix+'/caregiverCurriculum/updateByUserIdAndInitial/'+this.userId+'/'+this.is_initial;
          axios.put(asksUri, {
            classIdList: class_list,
            assigned_date: date,
            assigned_by: this.assignedBy
          })
          .then(response => {
            // this.caregiverCurriculums = response.data;
            var data = {
              'user_id':this.userId,
              'classIdList':class_list
            }
            
            //axios.post('http://localhost:8100/onlinecourses_wp/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
            axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
              console.log('response==>'+response.data['status']);
              if(response.data['status'] == 'success')
              {
                miniToastr.success('Removed Class Successfully!', 'Success');
                this.$emit('getAllCaregiverCurriculum', this.userId);
                this.isLoading = false;
              }
            });

          })
          .catch(function (error) {
            miniToastr.error('something went wrong!', 'Error');
            console.log(error);
        });
        
      });
    }
  }
}
</script>

