<template>
  <div class="animated fadeIn manage-classes">
    <b-row>
      <b-col xs="12" lg="12" class="curriculum-content">
        <b-card class="curriculum-card">
          <template slot="title">
            <i class="icon-people"></i>
          </template>
          <b-card-body>
            <b-tabs class="curriculum-tabs">
              <b-tab active>
                <template slot="title">
                  <i class="icon-people"></i> Initial Training
                </template>
                <!-- <b-alert show variant="secondary"><i class="icon-info icons"></i>&nbsp;Due immediately upon hire.</b-alert> -->
                <b-button block pill size="sm" @click="manageClassesModal = true; is_initial=1;" variant="primary" class="form-control btn-manage-classes ml-auto w-auto">Manage Classes</b-button>
                <CurriculumTable :columns="columns" :data="caregiverInitialCourseList" :options="options" :theme="theme" v-on:getAllCaregiverCurriculum="getAllCaregiverCurriculum"></CurriculumTable>
              </b-tab>
              <b-tab>
                <template slot="title">
                  <i class="icon-calendar"></i> Annual Compliance
                </template>
                <b-alert show variant="secondary"><i class="icon-info icons"></i>&nbsp;Due 1 year after initial/annual training was last completed (training anniversary). This curriculum will be repeated every year unless you add a second-year curriculum (using the ADD YEAR button).</b-alert>
                <b-button block pill size="sm" @click="manageClassesModal = true; is_initial=0;" variant="primary" class="form-control btn-manage-classes ml-auto w-auto">Manage Classes</b-button>
                <CurriculumTable :columns="columns" :data="caregiverAnnualCourseList" :options="options" :theme="theme"  v-on:getAllCaregiverCurriculum="getAllCaregiverCurriculum"></CurriculumTable>
              </b-tab>
            </b-tabs>
          </b-card-body>          
        </b-card>
      </b-col>
    </b-row>
    <b-modal title="Create Caregiver" class="modal-primary modal-header manage-classes" v-model="manageClassesModal" @ok="manageClassesModal = false">
      <template v-slot:modal-header>
        <b-button size="lg" variant="primary" class="btn-4x modal-header-icon"><i class="cui-paperclip icons"></i></b-button>&nbsp;&nbsp;<h4>Class Catalog</h4> 
      </template>
      <div md="12" sm="12">
        <!-- <CaregiverRegister :userId="userId" v-on:closeNewCaregiverModalDialog="closeNewCaregiverModal"></CaregiverRegister> -->
        <span v-for="(data, index) in caregiverClassList" :key="index">
          <ClassContainer :data="data"></ClassContainer>
        </span>
      </div>
      <div slot="modal-footer" class="w-100 btn-manage">
          <b-button size="md-4" pill class="mr-1 btn-manage-classes" type="submit" variant="primary" @click="saveManageClasses()" block>
            Save
          </b-button>
            <b-button size="md" pill class="btn-manage-classes-cancel" variant="secondary" @click="closeManageClasses()">
            Cancel
          </b-button>
      </div>
    </b-modal>
    <div class="vld-parent">
        <loading :active.sync="isLoading" 
        :can-cancel="true" 
        :is-full-page="fullPage"
        color="#06b">
        </loading>

    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import CurriculumTable from './CurriculumTable.vue'
import ClassContainer from './pages/ClassContainer'
import {restapiPrefix,wprestapiPrefix} from './variables.js'
import router from '../router';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import miniToastr from 'mini-toastr';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

export default {
  name: 'curriculum',
  components: {
    CurriculumTable,
    ClassContainer,
    Loading
  },
  data: function ()  {
    return {
      columns: ['class_name', 'duration', 'topic'],
      manageClassesModal: false,
      is_initial: 1,
      userId: 1,
      assignedBy: "Segun Ogungbemi",
      isLoading: false,
      fullPage: true,
      // data: sampleData,
      caregiverClassList: [], //Get all class list and active/deactive info for popup menu
      topicList: [], //caregivers_topic value
      categoryList: [], //courses_categoty value
      caregiverCurriculums: [], //caregivers_curriculum  values
      caregiverActiveClassList: [], //get active caregiver class list for curriculum table
      caregiverInitialCourseList: [], //get active caregiver class list for curriculum table
      caregiverAnnualCourseList: [], //get active caregiver class list for curriculum table
      options: {
        headings: {
          class: 'Class',
          duration: 'Duration',
          topics: 'Topics',
        },
        sortable: ['class'],
        filterable: ['class', 'topic'],
        sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
        pagination: {
          chunk: 5,
          edge: false,
          nav: 'scroll'
        }
      },
      useVuex: false,
      theme: 'bootstrap4',
      template: 'default'
    }
  },
  beforeCreate: function () {
      if(localStorage.user_id == undefined || localStorage.user_id == "0")
      {        
        router.push({ name: "Login"})
      }   
      /*var self = this
      alert('innn');
      if (!self.$session.exists()) {
        alert('nooo');
        this.$router.push('/')
      }
      else{
        alert('yess');
        return true;
      }*/
  },
  created() {
    this.userId = localStorage.user_id;
    this.assignedBy = localStorage.user_nm;
    //this.getAllCaregiverCurriculum();
    this.isLoading = true;
    this.getAllTopicList();
    this.getCategory();
    this.getAllCaregiverCurriculum();
    //this.getAllCaregiverClassList(); 
  },
  methods: {
    getAllCaregiverClassList() {
      // Get Caregiver groups by logged in user's Id
      let asksUri = restapiPrefix+'/caregiversClass/getCaregiversClassList';
      this.axios.get(asksUri).then(response => {
        this.caregiverClassList= [];
        //this.caregiverActiveClassList = [];
        this.caregiverInitialCourseList = [];
        this.caregiverAnnualCourseList = [];
        for(let i = 0; i < response.data.length; i++) {
          let caregiverClass = response.data[i];
          caregiverClass['topics'] = this.getTopicByClassId(response.data[i]['id']);
          caregiverClass['category'] = this.getCategoryByClassId(response.data[i]['id']);

          //check if the class is active or not by UserId, ClassId and Initial value
          caregiverClass['active'] = this.checkExistTopic(response.data[i]['id'], this.is_initial);
          /*var Assignedindex = Object.keys(this.caregiverCurriculums).filter(function(key) {
              return this.caregiverCurriculums[key] == response.data[i]['id'];
          })*/
          caregiverClass['assignedBy'] = this.getAssignedDetails(response.data[i]['id'],'assignedBy');//this.caregiverCurriculums[Assignedindex].assigned_by;
          caregiverClass['assignDate'] = this.getAssignedDetails(response.data[i]['id'],'assignedDate');//this.caregiverCurriculums[Assignedindex].assigned_date;
          caregiverClass['readMoreActivated'] = false;
          if (caregiverClass['active'] == "true") {
            //this.caregiverActiveClassList.push(caregiverClass);  
            if(caregiverClass['category']=='annual')
            {
              this.caregiverAnnualCourseList.push(caregiverClass);
            }
            else{
              this.caregiverInitialCourseList.push(caregiverClass);  
            }
          }
          this.caregiverClassList.push(caregiverClass);     
          this.isLoading = false;       
        }
      });
      
    },
    checkExistTopic(classId, is_initial) {
      for(let i = 0; i < this.caregiverCurriculums.length; i++) {
        if ((this.caregiverCurriculums[i]['class_id'] == classId) && (this.caregiverCurriculums[i]['class_type'] == is_initial)) {
          return "true";
        }
      }
      return "false";
    },
    getAssignedDetails(classId,type){
      for(let i = 0; i < this.caregiverCurriculums.length; i++) {
        if ((this.caregiverCurriculums[i]['class_id'] == classId)) {
          if(type == 'assignedBy')
          {
            return this.caregiverCurriculums[i].assigned_by;
          }
          else{
            var date = new Date(this.caregiverCurriculums[i].assigned_date);
            var d = ((date.getMonth() > 8) ? (date.getMonth() + 1) : ('0' + (date.getMonth() + 1))) + '/' + ((date.getDate() > 9) ? date.getDate() : ('0' + date.getDate())) + '/' + date.getFullYear();
            return d;
          }
        }
      }
      return "false";
    },
    getAllTopicList() {
      let asksUri = restapiPrefix+'/caregiversTopic/getAllCaregiversTopicList';
      this.axios.get(asksUri).then(response => {
        this.topicList = response.data;
      });
    },
    getTopicListByClassId(classId) {
      let asksUri = restapiPrefix+'/caregiversTopic/getCaregiversTopicListByClassId/'+classId;
      this.axios.get(asksUri).then(response => {
      });
    },

    getCategory(course_id)
    {
      let asksUri = restapiPrefix+'/caregiversClass/getCourseCategory';
      this.axios.get(asksUri).then(response => {
        console.log(response);
        this.categoryList = response.data;

      });
    },
    
    getTopicByClassId(classId) {
      let topics = [];
      // console.log(this.topicList)
      for(let i = 0; i < this.topicList.length; i++) {
        if (this.topicList[i]['class_id'] == classId) {
          topics.push(this.topicList[i]['topic_name']);
        }
      }
      return topics;
    },
    getCategoryByClassId(classId) {
      let categories = [];
      // console.log(this.topicList)
      for(let i = 0; i < this.categoryList.length; i++) {
        if (this.categoryList[i]['class_id'] == classId && this.categoryList[i]['slug']!='default') {
          categories.push(this.categoryList[i]['slug']);
        }
      }
      return categories;
    },
    closeManageClasses() {
      this.getAllCaregiverClassList();
      this.manageClassesModal = false;
    },
    saveManageClasses() {
      this.isLoading = true;
      let classIdList = [];
      for (let i = 0; i < this.caregiverClassList.length; i++) {
        if(this.caregiverClassList[i]['active'] == "true") {
          // curri['class_id'] = this.caregiverClassList[i]['id'];
          classIdList.push(this.caregiverClassList[i]['id']);
        }
      }

      if(classIdList.length == 0)
      {
          let asksUri_3 = restapiPrefix+'/caregiversClass/getDefaultCourses';
           this.axios.get(asksUri_3).then(response => {
              for(let i = 0; i < response.data.length; i++) {
                classIdList[i]=response.data[i]['class_id'];
              }
              //console.log('classList==>'+classList);
              this.assignCourses(classIdList);
            });
      }
      else{
        this.assignCourses(classIdList);
      }
    },
    assignCourses(classIdList)
    {
      var me= this;
      var date = new Date().toJSON().slice(0,10).replace(/-/g,'/')
      let asksUri = restapiPrefix+'/caregiverCurriculum/updateByUserIdAndInitial/'+this.userId+'/'+this.is_initial;
        axios.put(asksUri, {
          classIdList: classIdList,
          assigned_date: date,
          assigned_by: this.assignedBy
        })
        .then(response => {
          // this.caregiverCurriculums = response.data;
          var data = {
            'user_id':this.userId,
            'classIdList':classIdList
          }
          
          //axios.post('http://localhost:8100/onlinecourses_wp/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
          axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
            console.log('response==>'+response.data['status']);
            if(response.data['status'] == 'success')
            {
              miniToastr.success('Saved Classes Successfully!', 'Success');
              this.getAllCaregiverCurriculum();
            }
          });

        })
        .catch(function (error) {
          console.log(error);
          me.isLoading = false;
          miniToastr.error('something went wrong!', 'Error');
        });
      
      this.manageClassesModal = false;
    },
    getAllCaregiverCurriculum() {
      let asksUri = restapiPrefix+'/caregiverCurriculum/getAllCurriculumsByUserIdAndInitial/'+this.userId+'/'+this.is_initial;
      this.axios.get(asksUri).then(response => {
        this.caregiverCurriculums = response.data;
        this.getAllCaregiverClassList(); 
      });
    }
  },
  computed:{
  //   filteredName(){
  //     return this.data
  //       .filter((initial)=>{
  //         return item.initial.match(100);
  //     })
  }
}
</script>


